import React from 'react';
import { Link } from 'gatsby';
import Fade from 'react-reveal/Fade';
import './category-block.css';
import akfaUniversity from '../../images/akfa-university.jpg';
import bon from '../../images/bon.jpg';
import adrenaline from '../../images/adrenaline.jpg';

const CategoryBlock = () => (
    <div className="category-block">
        <Fade bottom>
            <p className="description">Our app development agency team builds mobile, web, Apple TV, and Apple Watch applications using Swift, Kotlin and ReactJS for innovative brands and companies from the San Francisco Bay Area and other big technology hubs.</p>
            <div className="card-column-3">
                <div className="card">
                    <div className="content">
                        <Link to="/project" className="card-video"
                         state={{ title: 'Akfa University', videoUrl: 'https://youtube.com/embed/2y6uzBOldDA' }}>
                            <div className="card-video-inner">
                                <img src={akfaUniversity} alt="Akfa University" />
                            </div>
                            <p>Apple TV app UI development</p>
                        </Link>
                    </div>
                </div>
                <div className="card">
                    <div className="content">
                        <Link to="/project" className="card-video"
                         state={{ title: 'Bon! Chain of French Coffee Houses', videoUrl: 'https://youtube.com/embed/Pb6hHX_0XKU' }}>
                            <div className="card-video-inner">
                                <img src={bon} alt="Bon" />
                            </div>
                            <p>Redesign of the support portal and 30% improvement in users’ ability to get the answers they want</p>
                        </Link>
                    </div>
                </div>
                <div className="card">
                    <div className="content">
                        <Link to="/project" className="card-video"
                         state={{ title: 'Adrenaline Rush Uzbekistan', videoUrl: 'https://www.youtube.com/embed/258IWEORXPI' }}>
                            <div className="card-video-inner">
                                <img src={adrenaline} alt="Adrenaline Rush" />
                            </div>
                            <p>Developing micro-interactions for the Lightning Design System</p>
                        </Link>
                    </div>
                </div>
            </div>
        </Fade>
    </div>
);

export default CategoryBlock;